
import tabBar from '../../components/TabBar.vue';
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import tools from '../../lib/tools.js';
import myService from '../../components/myService';
import moduleTitle from '../../components/ModuleTitle';
export default {
name:'MyService',
components:{Tab, TabItem,Flexbox, FlexboxItem,myService,moduleTitle,Toast,tabBar},
directives: {TransferDom},
data(){
return {
id:'',
info:{},
tabItem:true,
showToast:false,
toastMsg:'',
myService:[]
}
},
mounted(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));//md === userid
if((!id) || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 

this.id = id;
let pageSize=1000,pageNum=1;
let data = {"showCount": pageSize,"currentPage": pageNum,"userId":id,"orderCode": '',"relationName": "","relationSupplier": "","startDate": "","endDate": "","type": "1","payStatus": '1'};
this.$http.axios({method:'post',url:'/orderInfo/getOrderReportCount',data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
vm.myService = data.items;
console.log(vm.myService);
}
}else{
vm.showToast = true;
vm.toastMsg = '';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
});
this.toastMsg = '';
this.myDoctor();
},
methods:{
toggleTab(type){
this.tabItem = type;
},
myDoctor(){
let vm = this;

this.$http.get('/api/contracte/queryDoctors?managerId='+this.id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
if(typeof vm.info.createTime === 'string'){
vm.info.createTime = vm.info.createTime.replace(/\-/g, "/");
}
if(typeof vm.info.expirationDate === 'string'){
vm.info.expirationDate = vm.info.expirationDate.replace(/\-/g, "/");
}
vm.info.createTime = tools.simpleDateFormat(vm.info.createTime,'yyyy.MM.dd');
vm.info.expirationDate = tools.simpleDateFormat(vm.info.expirationDate,'yyyy.MM.dd');
}
}else{
vm.showToast = true;
vm.toastMsg = '';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
goToDoctor(){
tools.next('/m/doctorIndex.html?md='+this.info.managerId)
}
}
}



