
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import tools from '../lib/tools.js';
const FastClick = require('fastclick')
FastClick.attach(document.body);

var host = 'workflow-api.semioe.com';
if(window.location.host == 'cs.semioe.com'){
host = 's-workflow-api.semioe.com';
}

export default {
name:'serviceList',
components:{Flexbox, FlexboxItem, XDialog,Toast,Swipeout, SwipeoutItem, SwipeoutButton,Confirm},
directives: {TransferDom},
props:['services'],
data(){
return{
showToast:false,
toastMsg:'',
showDelConfirm:false,
delId:''
}
},
methods:{
goTo(id){
if(!id){return;}
window.location.href='/m/serviceIndex.html?serviceId='+id+'&_='+(new Date().getTime());//mdid
},
useService(sv){
this.showToast = true;
this.toastMsg = '...';
var vm = this;
this.$http.axios({method:'post',url:'/procedures/getProcedureDialog?userId='+sv.userId+'&doctorId='+sv.relationSupplierId+'&orderId='+sv.id+'&procedureId='+sv.relationCode}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
console.log(data.data.id);
let targetUrl = "http://"+host+"/v1/procedure-dialog/"+data.data.id;
console.log(targetUrl);
vm.showToast = false;
window.location.href=targetUrl;
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(vm.toastMsg);
}
},(error) => {
this.showToast = false;
this.toastMsg = '';
console.log(error);
});
},
xDialog(orderId){
window.location.href="../m/serviceReport.html?orderId="+orderId;
},
delService(id){
this.showDelConfirm = true;
this.delId = id;
},
onJGConfirm(){
let vm = this;
this.$http.axios({method:'get',url:'/orderInfo/deleteOrderInfo?id='+this.delId}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = '';
vm.services = vm.services.filter(item => item.id!=vm.delId);
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
});
}
}
}



